    legalPromptMessage: TemplateFunction<
        readonly ["privacyPolicyUrl", "termsOfServiceUrl", "learnMoreUrl"],
        {
            privacyPolicyUrl: string;
            termsOfServiceUrl: string;
            learnMoreUrl: string;
        }
    >;
    legalPromptAccept: string;
    legalPromptReject: string;
